/* ---------------------------- Types/Consts ---------------------------- */

type IssueKind =
  | "indent"
  | "bullet-style"
  | "numbered-style"
  | "figure-empty"
  | "figure-style"
  | "table-empty"
  | "table-style";

type Issue = { tag: string; kind: IssueKind; message: string; fixMeta?: any };

const ISSUE_TAG_PREFIX = "sgc-";
const POINTS_PER_CM = 28.3464567;
const toCm = (pt: number) => (pt || 0) / POINTS_PER_CM;
const toPt = (cm: number) => (cm || 0) * POINTS_PER_CM;
const round2 = (v: number) => Math.round(v * 100) / 100;

const BATCH_SIZE = 500; // Reduced batch size for better stability

/* ------------------------------ Globals ------------------------------ */

function getRules(): any { return (window as any).styleGuideRules; }
function setRules(obj: any) { (window as any).styleGuideRules = obj; }

/* ------------------------------ Utilities with Error Handling ------------------------------ */

function normalizeStyle(name: string, aliases?: Record<string, string>) {
  try {
    const raw = (name || "").toString().trim();
    const base = raw.replace(/\s+/g, " ").toLowerCase();
    if (!aliases) return base;
    for (const [k, v] of Object.entries(aliases)) {
      const kk = (k || "").toString().trim().replace(/\s+/g, " ").toLowerCase();
      if (kk === base) return (v || "").toString().trim().replace(/\s+/g, " ").toLowerCase();
    }
    return base;
  } catch (error) {
    console.warn("Error normalizing style:", error);
    return (name || "").toString().trim().toLowerCase();
  }
}

function normalizeStyleList(input: string | string[], aliases?: Record<string, string>) {
  try {
    const arr = Array.isArray(input) ? input : [input];
    const set = new Set<string>();
    for (const n of arr) { 
      set.add(n); 
      set.add(normalizeStyle(n, aliases)); 
    }
    return { 
      set, 
      has: (raw: string) => {
        try {
          return set.has(raw) || set.has(normalizeStyle(raw, aliases));
        } catch {
          return false;
        }
      }
    };
  } catch (error) {
    console.warn("Error creating normalized style list:", error);
    return { set: new Set<string>(), has: () => false };
  }
}

function labelsToArray(v: any, fallback: string[]) {
  try {
    if (Array.isArray(v)) return v;
    if (typeof v === "string" && v.trim()) return [v.trim()];
    return fallback;
  } catch {
    return fallback;
  }
}

let __issueCounter = 0;
function newIssueTag() { __issueCounter += 1; return String(__issueCounter).padStart(6, "0"); }

/* Marker: hidden content control with better error handling */
async function markParagraph(ctx: Word.RequestContext, p: Word.Paragraph): Promise<string> {
  try {
    const tag = newIssueTag();
    const r = p.getRange();
    const cc = r.insertContentControl();
    cc.tag = ISSUE_TAG_PREFIX + tag;
    cc.title = "Style Guide Issue";
    cc.appearance = "Hidden";
    
    // Ensure sync happens immediately for content control creation
    await ctx.sync();
    return tag;
  } catch (error) {
    console.error("Error marking paragraph:", error);
    // Return a fallback tag even if marking fails
    return newIssueTag();
  }
}

/* ------------------------------ Enhanced TOC Skip ------------------------------ */

function isTocStyleName(rawOrStd: string): boolean {
  try {
    const s = (rawOrStd || "").toString().trim().toLowerCase();
    return /^toc(\s*\d+)?$/.test(s) || 
           s === "toc heading" || 
           s === "" || 
           /\s*\d+/.test(s) ||
           s === "table of contents" ||
           /^contents(\s*\d+)?$/.test(s) ||
           /^(\s*\d+)?$/.test(s) ||
           s === "" ||
           s.includes("table of contents") ||
           s.includes("");
  } catch {
    return false;
  }
}

function isLikelyTocText(text: string): boolean {
  try {
    const t = (text || "").replace(/\u00A0/g, " ").trim();
    const reLeaderTabPage = /[.\u2026\u00B7\u2219]{3,}\s*\t+\s*\d+$/;
    const reLongLeaderNumber = /[.\u2026\u00B7\u2219]{5,}\s*\d+$/;
    const reSpacesNumber = /[.\u2026\u00B7\u2219]{3,}\s{2,}\d+$/;
    const reTabNumber = /\t+\s*\d+$/;
    const reMultipleSpacesNumber = /\s{3,}\d+$/;
    const reChapterPattern = /\d+\.\d+.*[\.\u2026\u00B7\u2219]{2,}.*\d+$/;
    
    return reLeaderTabPage.test(t) || 
           reLongLeaderNumber.test(t) || 
           reSpacesNumber.test(t) ||
           reTabNumber.test(t) ||
           reMultipleSpacesNumber.test(t) ||
           reChapterPattern.test(t);
  } catch {
    return false;
  }
}

async function computeTocSkips(ctx: Word.RequestContext, paras: Word.ParagraphCollection): Promise<boolean[]> {
  try {
    const skip: boolean[] = new Array(paras.items.length).fill(false);

    // Enhanced style/text checks
    paras.items.forEach((p, i) => {
      try {
        const raw = (p.style || "").toLowerCase();
        const txt = (p.text || "").trim();

        if (isTocStyleName(raw) || isLikelyTocText(txt)) {
          skip[i] = true;
          return;
        }

        if (txt.toLowerCase().includes("table of contents") ||
            txt.toLowerCase().includes("") ||
            txt.toLowerCase().includes("")) {
          skip[i] = true;
        }
      } catch (error) {
        console.warn(`Error checking TOC for paragraph ${i}:`, error);
        // If there's an error, don't skip by default
      }
    });

    // Field-based TOC detection with comprehensive error handling
    try {
      const fields = ctx.document.body.getRange().fields;
      fields.load("items");
      await ctx.sync();

      if (fields.items && fields.items.length > 0) {
        for (const f of fields.items as any[]) {
          try {
            (f as any).load?.("code");
          } catch (fieldError) {
            console.warn("Error loading field code:", fieldError);
          }
        }
        
        try {
          await ctx.sync();
        } catch (syncError) {
          console.warn("Error syncing field codes:", syncError);
          return skip; // Return what we have from style/text checks
        }

        for (const f of fields.items as any[]) {
          try {
            const code = String((f as any)?.code ?? "");
            if (/^\s*TOC\b/i.test(code)) {
              const tocRange = (f as any).getRange?.();
              if (tocRange) {
                const tocParas = tocRange.paragraphs;
                tocParas.load("items");
                await ctx.sync();
                
                for (const tp of tocParas.items) {
                  try {
                    const text = tp.text;
                    const style = tp.style;
                    paras.items.forEach((p, i) => {
                      if (p.text === text && p.style === style) {
                        skip[i] = true;
                      }
                    });
                  } catch (tocParaError) {
                    console.warn("Error processing TOC paragraph:", tocParaError);
                  }
                }
              }
            }
          } catch (fieldProcessError) {
            console.warn("Error processing individual field:", fieldProcessError);
          }
        }
      }
    } catch (fieldsError) {
      console.warn("Error with field-based TOC detection:", fieldsError);
      // Continue with style/text-based detection only
    }

    return skip;
  } catch (error) {
    console.error("Critical error in computeTocSkips:", error);
    // Return safe default - don't skip any paragraphs if we can't determine
    return new Array(paras.items?.length || 0).fill(false);
  }
}

/* ------------------------------ Enhanced List Checker (Bullets + Numbered) ------------------------------ */

async function checkBulletedListCompliance(
  ctx: Word.RequestContext,
  paragraph: Word.Paragraph,
  rules: any,
  aliases: Record<string, string>,
  issues: Issue[]
): Promise<void> {
  
  try {
    const raw = paragraph.style || "";
    const std = normalizeStyle(raw, aliases);
    const txt = paragraph.text || "";

    console.log("[DEBUG] Checking paragraph: \"" + txt.substring(0, 50) + "...\" | Style: \"" + raw + "\" | Normalized: \"" + std + "\"");

    // Skip placeholder text controls and empty paragraphs
    if (txt.includes("    ") || 
        txt.includes("Click or tap here to enter text") ||
        txt.includes("Type here") ||
        txt.trim() === "" ||
        /^(\[.*\]|\{.*\}|\(.*\))$/.test(txt.trim())) {
      console.log("[DEBUG] Skipping placeholder/empty paragraph");
      return;
    }

    // Skip TOC-like content
    if (isTocStyleName(raw) || isTocStyleName(std) || isLikelyTocText(txt)) {
      console.log("[DEBUG] Skipping TOC-like content");
      return;
    }

    // Extract rules from the configuration
    const bulletRule = rules?.rules?.styles?.bullets ?? {};
    const numberedRule = rules?.rules?.styles?.numberedLists ?? {};
    
    const allowedBulletNames: string[] = (bulletRule.allowed ?? [
      "TCC 1", "TCC 2", "TCC 3", "TCC 4"
    ]);
    const allowedNumberedNames: string[] = (numberedRule.allowed ?? [
      "TCC 1", "TCC 2", "TCC 3"
    ]);
    
    const allowedBulletIndentsCm: number[] = (bulletRule.allowedIndentsCm ?? [0.71, 1.21, 1.71, 2.21]);
    const allowedNumberedIndentsCm: number[] = (numberedRule.allowedIndentsCm ?? [0.71, 1.21, 1.71]);
    
    // Combine all allowed list styles
    const allAllowedListNames = [...allowedBulletNames, ...allowedNumberedNames];
    const allAllowedIndents = [...allowedBulletIndentsCm, ...allowedNumberedIndentsCm];
    
    // Create normalized set of allowed list styles
    const allowedListStyles = new Set<string>();
    allAllowedListNames.forEach(styleName => {
      allowedListStyles.add(styleName);
      allowedListStyles.add(normalizeStyle(styleName, aliases));
    });

    // Get indentation policy and heading info
    const indentPolicy: Record<string, number> = rules?.rules?.indentation?.expectedCmByStyle || {};
    const indentTolCm = Math.max(rules?.rules?.indentation?.toleranceCm ?? 0.03, 0.02);
    
    const headingAllowedRaw: string[] = (rules?.rules?.styles?.headings?.allowed ?? []) as string[];
    const HEADING_ALLOWED = new Set(headingAllowedRaw.map(s => normalizeStyle(s, aliases)));
    
    // Check if this is a heading style - if so, skip list processing
    const isHeading =
      HEADING_ALLOWED.has(std) ||
      HEADING_ALLOWED.has(raw) ||
      /^(heading|)\s*\d+/.test(std) ||
      /^(heading|)\s*\d+/.test((raw||"").toLowerCase());
    
    if (isHeading) {
      console.log("[DEBUG] Skipping heading style:", raw);
      return;
    }

    // Enhanced list detection
    const trimmedText = txt.trim();
    
    // First check if text looks like a heading
    const looksLikeHeading = /^(\d+\.)*\d+\s+[A-Z]/.test(trimmedText) ||
                            /^\d+\.\s+[A-Z]/.test(trimmedText) ||        
                            /^Chapter\s+\d+/i.test(trimmedText) ||       
                            /^\s*\d+/i.test(trimmedText);              
    
    if (looksLikeHeading) {
      console.log("[DEBUG] Skipping heading-like text:", trimmedText.substring(0, 30));
      return;
    }
    
    const listHeuristics = {
      hasBulletChar: /^[\u2022\u25E6\u25AA\u25CF\u25CB\u25A0]\s+/.test(trimmedText),
      hasDashBullet: /^[\-]\s+/.test(trimmedText),
      hasNumbering: /^(\(?\d{1,2}\)|(\d{1,2}[\.)]))(\s+)/.test(trimmedText) && !looksLikeHeading,
      hasLettering: /^(\(?[A-Za-z]\)|([A-Za-z][\.)]))(\s+)/.test(trimmedText),
      hasRoman: /^(\(?[ivxlc]{1,4}\)|([ivxlc]{1,4}[\.)]))(\s+)/i.test(trimmedText),
      isWordList: !!paragraph.isListItem,
      hasTabBullet: /^\t+[\u2022\u25E6\u25AA\u25CF\u25CB\u25A0\-]\s+/.test(trimmedText),
      hasSpaceBullet: /^\s{2,}[\u2022\u25E6\u25AA\u25CF\u25CB\u25A0\-]\s+/.test(trimmedText)
    };
    
    const hasVisualListMarkers = listHeuristics.hasBulletChar || 
                                listHeuristics.hasDashBullet ||
                                listHeuristics.hasNumbering || 
                                listHeuristics.hasLettering ||
                                listHeuristics.hasRoman ||
                                listHeuristics.hasTabBullet ||
                                listHeuristics.hasSpaceBullet;
    
    const isAnyKindOfList = listHeuristics.isWordList || hasVisualListMarkers;
    const isRealList = listHeuristics.isWordList && hasVisualListMarkers;
    const hasWrongStyleList = hasVisualListMarkers && !listHeuristics.isWordList;
    
    const isBulletType = listHeuristics.hasBulletChar || listHeuristics.hasDashBullet;
    const isNumberedType = listHeuristics.hasNumbering || listHeuristics.hasLettering || listHeuristics.hasRoman;
    
    // Only check paragraphs that actually look like lists
    if (!isAnyKindOfList) {
      console.log("[DEBUG] Not a list, skipping list checks");
      return;
    }

    // Calculate current indent for fixMeta
    const leftCm = toCm((paragraph.leftIndent as any) || 0);
    const firstCm = toCm((paragraph.firstLineIndent as any) || 0);
    const currentIndent = leftCm + firstCm;

    // Check 1: Style compliance for all list-like paragraphs
    const hasCorrectListStyle = allowedListStyles.has(raw) || allowedListStyles.has(std);
    
    if (!hasCorrectListStyle) {
      console.log("[DEBUG]  STYLE ISSUE FOUND - adding to issues");
      
      try {
        const tag = await markParagraph(ctx, paragraph);
        
        let allowedStylesDisplay: string;
        let issueKind: IssueKind;
        let fixMeta: any;
        
        if (isBulletType) {
          allowedStylesDisplay = Array.from(new Set(allowedBulletNames)).join(", ");
          issueKind = "bullet-style";
          fixMeta = { 
            currentIndent: currentIndent,
            allowedStyles: allowedBulletNames,
            allowedIndents: allowedBulletIndentsCm
          };
        } else if (isNumberedType) {
          allowedStylesDisplay = Array.from(new Set(allowedNumberedNames)).join(", ");
          issueKind = "numbered-style";
          fixMeta = { 
            currentIndent: currentIndent,
            allowedStyles: allowedNumberedNames,
            allowedIndents: allowedNumberedIndentsCm
          };
        } else {
          allowedStylesDisplay = Array.from(new Set(allAllowedListNames)).join(", ");
          issueKind = "bullet-style";
          fixMeta = { 
            currentIndent: currentIndent,
            allowedStyles: allowedBulletNames,
            allowedIndents: allowedBulletIndentsCm
          };
        }
        
        issues.push({
          tag,
          kind: issueKind,
          message: "List paragraph must use: " + allowedStylesDisplay + " (found \"" + (raw || std || "unknown") + "\").",
          fixMeta: fixMeta
        });
      } catch (markError) {
        console.error("Error marking paragraph for style issue:", markError);
        // Continue processing other issues even if marking fails
      }
    }

    // Check 2: Indent compliance (only for real lists with proper formatting)
    if (isRealList) {
      try {
        const textStartIndent = currentIndent;

        // Check if there's a specific indent policy for this style
        let expectedIndent: number | undefined;
        
        if (indentPolicy[raw]) {
          expectedIndent = indentPolicy[raw];
        } else {
          const normalizedStyle = normalizeStyle(raw, aliases);
          if (indentPolicy[normalizedStyle]) {
            expectedIndent = indentPolicy[normalizedStyle];
          }
        }

        if (expectedIndent !== undefined) {
          const indentDifference = Math.abs(textStartIndent - expectedIndent);
          
          if (indentDifference > indentTolCm) {
            console.log("[DEBUG]  INDENT ISSUE FOUND (specific policy) - adding to issues");
            try {
              const tag = await markParagraph(ctx, paragraph);
              issues.push({
                tag,
                kind: "indent",
                message: "Indent " + round2(textStartIndent) + " cm (expected " + round2(expectedIndent) + " cm) for style \"" + (raw || std) + "\".",
                fixMeta: { expectedCm: expectedIndent }
              });
            } catch (markError) {
              console.error("Error marking paragraph for indent issue:", markError);
            }
          }
        } else {
          // No specific policy - check against appropriate allowed indent tiers
          let allowedIndentsCm: number[];
          
          if (isBulletType) {
            allowedIndentsCm = allowedBulletIndentsCm;
          } else if (isNumberedType) {
            allowedIndentsCm = allowedNumberedIndentsCm;
          } else {
            allowedIndentsCm = allAllowedIndents;
          }
          
          const nearestAllowedIndent = allowedIndentsCm.reduce((best, cm) => {
            const distance = Math.abs(textStartIndent - cm);
            return distance < best.distance ? { cm, distance } : best;
          }, { 
            cm: allowedIndentsCm[0], 
            distance: Number.POSITIVE_INFINITY 
          });

          const tierTolerance = indentTolCm + 0.03;
          
          if (nearestAllowedIndent.distance > tierTolerance) {
            console.log("[DEBUG]  INDENT ISSUE FOUND (tier-based) - adding to issues");
            try {
              const tag = await markParagraph(ctx, paragraph);
              const allowedIndentsDisplay = allowedIndentsCm.map(v => round2(v)).join(", ");
              issues.push({
                tag,
                kind: "indent",
                message: "List indent " + round2(textStartIndent) + " cm not in " + allowedIndentsDisplay + " cm.",
                fixMeta: { expectedCm: nearestAllowedIndent.cm }
              });
            } catch (markError) {
              console.error("Error marking paragraph for tier indent issue:", markError);
            }
          }
        }
      } catch (indentError) {
        console.error("Error checking indent compliance:", indentError);
      }
    }
    
    console.log("[DEBUG] --- End of list check for this paragraph ---\n");
    
  } catch (error) {
    console.error("Error in checkBulletedListCompliance:", error);
    // Don't throw - continue processing other paragraphs
  }
}

/** After "<Label> 1[.2]*", return the remaining text (trimmed-left). */
function tailAfterLabelNumber(text: string, labels: string[]): string {
  try {
    const t = (text || "").replace(/\u00A0/g, " ");
    const labelAlt = labels.map(l => l.replace(/[.*+?^${}()|[\]\\]/g, "\\$&")).join("|");
    const re = new RegExp(
      "^\\s*(?:" + labelAlt + ")\\s*\\d+(?:\\.\\d+)*\\s*([\\s\\S]*)$",
      "i"
    );
    const m = re.exec(t);
    return m ? m[1].replace(/^\s+/, "") : "";
  } catch {
    return "";
  }
}

/** True if the tail clearly continues as a sentence (not a caption head). */
function hasRealSentenceAfterNumber(text: string, labels: string[]): boolean {
  try {
    const tail = tailAfterLabelNumber(text, labels);
    if (!tail) return false;

    if (/^[.:-]/.test(tail)) return false;

    const firstWord = /^[A-Za-z]+/.exec(tail);
    if (firstWord && firstWord[0].charAt(0) === firstWord[0].charAt(0).toLowerCase()) {
      return true;
    }

    const determiners = /\b(the|this|that|these|those|a|an|our|its?|their|there|here|we|it|they|will|can|should|would|could|may|might|must)\b/i;
    const earlyVerbs = /\b(shows?|illustrates?|lists?|presents?|summarizes?|depicts?|compares?|gives?|displays?|demonstrates?|indicates?|describes?|contains?|includes?|provides?|represents?|reveals?|explains?|outlines?|details?|reports?|records?|tracks?|measures?|analyzes?|examines?)\b/i;
    const explanatoryPatterns = /\b(is|are|was|were|has|have|had|does|do|did|will|would|can|could|should|may|might|must)\b/i;
    const conjunctions = /\b(and|but|or|so|yet|for|nor|because|since|although|though|while|whereas|however|therefore|thus|hence)\b/i;

    const firstEight = tail.split(/\s+/).slice(0, 8).join(" ");
    
    if (determiners.test(firstEight) || 
        earlyVerbs.test(firstEight) || 
        explanatoryPatterns.test(firstEight) ||
        conjunctions.test(firstEight)) {
      return true;
    }

    if (/^[([{}]/.test(tail)) return false;
    if (/[.!?]\s+[A-Z]/.test(tail)) return true;

    return false;
  } catch {
    return false;
  }
}

/* ------------------------------ Caption helpers ------------------------------ */

function joinLabelsToWordBoundary(labels: string[]) {
  try {
    return labels.map(l => l.replace(/[.*+?^${}()|[\]\\]/g, "\\$&")).join("|");
  } catch {
    return "";
  }
}

function looksLikeCaptionText(text: string, labels: string[]): boolean {
  try {
    const t = (text || "").replace(/\u00A0/g, " ");
    const re = new RegExp("^\\s*(?:" + joinLabelsToWordBoundary(labels) + ")\\b", "i");
    return re.test(t);
  } catch {
    return false;
  }
}

function descriptionAfterNumber(text: string, labels: string[]): string {
  try {
    const t = (text ?? "").replace(/\u00A0/g, " ");
    const head = new RegExp(
      "^\\s*(?:" + joinLabelsToWordBoundary(labels) + ")\\s*\\d+(?:\\.\\d+)*\\s*[:.\\-]?\\s*",
      "i"
    );
    return t.replace(head, "").trim();
  } catch {
    return "";
  }
}

async function hasChapterSeqForLabelRobust(
  ctx: Word.RequestContext,
  p: Word.Paragraph,
  label: "Table" | "Figure"
): Promise<boolean> {
  try {
    // First try the Field API approach with better error handling
    try {
      const fields = p.getRange().fields;
      fields.load("items");
      await ctx.sync();
      
      if (fields.items && fields.items.length) {
        const fieldPromises = fields.items.map(f => {
          try {
            return (f as any).load?.("code");
          } catch {
            return null;
          }
        }).filter(Boolean);
        
        if (fieldPromises.length > 0) {
          await ctx.sync();
          
          let hasStyle = false, hasSeq = false, hasArabic = false, hasReset = false;
          const reSeq = new RegExp("\\bSEQ\\s+" + label + "\\b", "i");
          
          for (const f of fields.items as any[]) {
            try {
              const code = String(f?.code ?? "");
              if (/\bSTYLEREF\s+1\s+\\s\b/i.test(code)) hasStyle = true;
              if (reSeq.test(code)) hasSeq = true;
              if (/\*\s*ARABIC\b/i.test(code)) hasArabic = true;
              if (/\\s\s*1\b/i.test(code)) hasReset = true;
            } catch (fieldError) {
              console.warn("Error processing field code:", fieldError);
            }
          }
          
          if (hasStyle && hasSeq && hasArabic && hasReset) return true;
        }
      }
    } catch (fieldApiError) {
      console.warn("Field API approach failed:", fieldApiError);
    }

    // Fallback to OOXML approach with better error handling
    try {
      const oox = p.getRange().getOoxml();
      await ctx.sync();
      const xml = String(oox.value || "");

      const hasFldSimpleStyle = /<w:fldSimple[^>]*\binstr="[^"]*\bSTYLEREF\s+1\s+\\s\b[^"]*"/i.test(xml);
      const hasFldSimpleSeq   = new RegExp(
        "<w:fldSimple[^>]*\\binstr=\"[^\"]*\\bSEQ\\s+" + label + "\\b[^\"]*\\*\\s*ARABIC[^\"]*\\\\s\\s*1[^\"]*\"",
        "i"
      ).test(xml);
      if (hasFldSimpleStyle && hasFldSimpleSeq) return true;

      const parts: string[] = [];
      const reInstr = /<w:instrText\b[^>]*>([\s\S]*?)<\/w:instrText>/gi;
      let m: RegExpExecArray | null;
      while ((m = reInstr.exec(xml)) !== null) { 
        parts.push(m[1]); 
        if (reInstr.lastIndex === m.index) reInstr.lastIndex++; 
      }
      
      if (parts.length) {
        const joined = parts.join("");
        const hasStyle = /\bSTYLEREF\s*1\s*\\s\b/i.test(joined);
        const hasSeq   = new RegExp("\\bSEQ\\s*" + label + "\\b", "i").test(joined);
        const hasArabic= /\*\s*ARABIC\b/i.test(joined);
        const hasReset = /\\s\s*1\b/i.test(joined);
        if (hasStyle && hasSeq && hasArabic && hasReset) return true;
      }
      
      return false;
    } catch (ooxmlError) {
      console.warn("OOXML approach failed:", ooxmlError);
      return false;
    }
  } catch (error) {
    console.error("Critical error in hasChapterSeqForLabelRobust:", error);
    return false;
  }
}

function stripManualPrefix(text: string, labels: string[]): string {
  try {
    const head = new RegExp(
      "^\\s*(?:" + joinLabelsToWordBoundary(labels) + ")\\s*[.\\-:]?\\s*\\d+(?:[.\\-]\\d+)*\\s*[.\\-:]?\\s*",
      "i"
    );
    return (text || "").replace(head, "").trimStart();
  } catch {
    return text || "";
  }
}

function prependLabelChapterSeqPrefix(range: Word.Range, label: "Table" | "Figure") {
  try {
    const L = label.replace(/[&<>"]/g, function(m) {
      const map: Record<string, string> = { "&":"&amp;","<":"&lt;",">":"&gt;",'"':"&quot;" };
      return map[m] || m;
    });
    const runs =
      "<w:r><w:t>" + L + " </w:t></w:r>" +
      "<w:r><w:fldChar w:fldCharType=\"begin\"/></w:r>" +
      "<w:r><w:instrText xml:space=\"preserve\"> STYLEREF 1 \\s </w:instrText></w:r>" +
      "<w:r><w:fldChar w:fldCharType=\"separate\"/></w:r>" +
      "<w:r><w:t>1</w:t></w:r>" +
      "<w:r><w:fldChar w:fldCharType=\"end\"/></w:r>" +
      "<w:r><w:t>.</w:t></w:r>" +
      "<w:r><w:fldChar w:fldCharType=\"begin\"/></w:r>" +
      "<w:r><w:instrText xml:space=\"preserve\"> SEQ " + L + " \\* ARABIC \\s 1 </w:instrText></w:r>" +
      "<w:r><w:fldChar w:fldCharType=\"separate\"/></w:r>" +
      "<w:r><w:t>1</w:t></w:r>" +
      "<w:r><w:fldChar w:fldCharType=\"end\"/></w:r>" +
      "<w:r><w:t xml:space=\"preserve\"> </w:t></w:r>";
    range.insertOoxml(runs, Word.InsertLocation.start);
  } catch (error) {
    console.error("Error inserting OOXML:", error);
  }
}

/* ------------------------------ Load Rules / Preview / Clear ------------------------------ */

function renderRulesPreview(rules: any) {
  try {
    let box =
      document.getElementById("rulesDump") ||
      document.getElementById("rulesPreview") ||
      document.getElementById("rulesBox");

    if (!box) {
      const statusEl = document.getElementById("rulesStatus");
      box = document.createElement("pre");
      box.id = "rulesPreview";
      const st = (box as HTMLElement).style;
      st.whiteSpace = "pre-wrap";
      st.wordBreak = "break-word";
      st.marginTop = "8px";
      st.padding = "8px 10px";
      st.border = "1px solid #ddd";
      st.borderRadius = "4px";
      st.background = "#f4f4f4";
      st.maxHeight = "260px";
      st.overflow = "auto";
      if (statusEl?.parentElement) statusEl.parentElement.appendChild(box);
      else document.body.appendChild(box);
    }

    const pretty = JSON.stringify(rules ?? {}, null, 2);
    const MAX = 80 * 1024;
    (box as HTMLElement).textContent = pretty.length > MAX ? pretty.slice(0, MAX) + "\n(truncated)" : pretty;
  } catch (error) {
    console.error("Error rendering rules preview:", error);
  }
}

function bindRulesLoader() {
  try {
    const fileInput = document.getElementById("rulesFileInput") as HTMLInputElement | null;
    const loadBtn   = document.getElementById("loadRulesBtn") as HTMLButtonElement | null;
    const statusEl  = document.getElementById("rulesStatus") as HTMLElement | null;

    const setStatus = (msg: string, color = "#555") => { 
      if (statusEl) { 
        statusEl.textContent = msg; 
        statusEl.style.color = color; 
      } 
    };
    
    const applyRules = (obj: any) => { 
      setRules(obj); 
      setStatus("Rules loaded.", "#0a0"); 
      renderRulesPreview(obj); 
    };

    async function readFile(f: File): Promise<boolean> {
      try {
        const txt = await f.text();
        const json = JSON.parse(txt);
        applyRules(json);
        return true;
      } catch (e) {
        console.error("[SGC] Failed to read/parse file:", e);
        setStatus("Invalid rules.json (cannot parse).", "#c00");
        return false;
      }
    }

    if (fileInput) {
      fileInput.accept = ".json,application/json";
      fileInput.addEventListener("change", async () => {
        if (!fileInput.files || fileInput.files.length === 0) return;
        await readFile(fileInput.files[0]);
      });
    }

    async function openPickerAndRead(): Promise<boolean> {
      if (fileInput) {
        return await new Promise<boolean>((resolve) => {
          fileInput.value = "";
          const handler = async () => {
            fileInput.removeEventListener("change", handler);
            if (fileInput.files && fileInput.files.length > 0) resolve(await readFile(fileInput.files[0]));
            else resolve(false);
          };
          fileInput.addEventListener("change", handler, { once: true });
          fileInput.click();
        });
      }
      return false;
    }

    if (loadBtn) {
      loadBtn.addEventListener("click", async () => {
        setStatus("Loading rules");
        if (fileInput?.files?.length) { 
          const ok = await readFile(fileInput.files[0]); 
          if (ok) return; 
        }
        const picked = await openPickerAndRead();
        if (!picked) setStatus("Please choose rules.json manually.", "#c00");
      });
    }
  } catch (error) {
    console.error("Error binding rules loader:", error);
  }
}

async function clearMarkers() {
  try {
    await Word.run(async (ctx) => {
      try {
        const all = ctx.document.contentControls;
        all.load("items/tag"); 
        await ctx.sync();
        
        // Clear highlighting first
        for (const cc of all.items) {
          if (!cc.tag || !cc.tag.startsWith(ISSUE_TAG_PREFIX)) continue;
          try {
            const r = cc.getRange();
            r.font.highlightColor = "NoColor";
            (r.font as any).highlightColor = "None";
          } catch (highlightError) {
            console.warn("Error clearing highlight:", highlightError);
          }
        }
        await ctx.sync();

        // Delete content controls
        const toDelete = all.items.filter(cc => cc.tag && cc.tag.startsWith(ISSUE_TAG_PREFIX));
        for (const cc of toDelete) {
          try {
            cc.delete(true);
          } catch (deleteError) {
            console.warn("Error deleting content control:", deleteError);
          }
        }
        await ctx.sync();

        // Clear any remaining highlights
        try {
          const paras = ctx.document.body.paragraphs;
          paras.load("items"); 
          await ctx.sync();
          
          for (const p of paras.items) {
            try {
              const r = p.getRange();
              r.font.highlightColor = "NoColor";
              (r.font as any).highlightColor = "None";
            } catch (paraHighlightError) {
              console.warn("Error clearing paragraph highlight:", paraHighlightError);
            }
          }
          await ctx.sync();

          const tables = ctx.document.body.tables;
          tables.load("items"); 
          await ctx.sync();
          
          for (const t of tables.items) {
            try {
              const r = t.getRange();
              r.font.highlightColor = "NoColor";
              (r.font as any).highlightColor = "None";
            } catch (tableHighlightError) {
              console.warn("Error clearing table highlight:", tableHighlightError);
            }
          }
          await ctx.sync();

          const allRange = ctx.document.body.getRange();
          allRange.font.highlightColor = "NoColor";
          (allRange.font as any).highlightColor = "None";
          await ctx.sync();
        } catch (globalClearError) {
          console.warn("Error with global highlight clearing:", globalClearError);
        }
      } catch (clearError) {
        console.error("Error in clearMarkers main logic:", clearError);
      }
    });
  } catch (error) {
    console.error("Critical error in clearMarkers:", error);
  }
}

/* ------------------------------ Scan with Better Error Handling ------------------------------ */

function bindScan() {
  try {
    const btn = document.getElementById("scanBtn") as HTMLButtonElement | null;
    const status = document.getElementById("scanStatus") as HTMLElement | null;
    const list = document.getElementById("issuesList") as HTMLOListElement | null;
    if (!btn || !status || !list) return;

    btn.addEventListener("click", async () => {
      try {
        const rules = getRules();
        if (!rules) { 
          status.textContent = "Load a rules JSON first."; 
          status.style.color = "#c00"; 
          return; 
        }

        status.textContent = "Scanning"; 
        status.style.color = "#555"; 
        list.innerHTML = "";
        
        const issues: Issue[] = [];
        const aliases: Record<string, string> = rules?.meta?.styleNameAliases || {};

        const indentPolicy: Record<string, number> = rules?.rules?.indentation?.expectedCmByStyle || {};
        const indentTolCm = Math.max(rules?.rules?.indentation?.toleranceCm ?? 0.03, 0.02);

        const headingAllowedRaw: string[] = (rules?.rules?.styles?.headings?.allowed ?? []) as string[];
        const HEADING_ALLOWED = new Set(headingAllowedRaw.map(s => normalizeStyle(s, aliases)));

        const capRules = rules?.rules?.styles?.captions || {};
        const figRule = capRules.figure || {};
        const tblRule = capRules.table || {};
        const figAllowed = normalizeStyleList(figRule.style ?? "TCC  ", aliases);
        const tblAllowed = normalizeStyleList((tblRule.styles ?? tblRule.style) ?? ",TCC  ,TCC", aliases);
        const FIG_LABELS = labelsToArray(figRule.seqLabels, ["Figure",""]);
        const TBL_LABELS = labelsToArray(tblRule.seqLabels, ["Table",""]);
        const requireFigSeq = figRule.requireAutoNumber !== false;
        const requireTblSeq = tblRule.requireAutoNumber !== false;

        // Clear existing markers first
        try {
          await Word.run(async (context) => {
            const all = context.document.contentControls;
            all.load("items/tag"); 
            await context.sync();
            
            const toDelete = all.items.filter(cc => cc.tag && cc.tag.startsWith(ISSUE_TAG_PREFIX));
            for (const cc of toDelete) {
              try {
                cc.delete(true);
              } catch (deleteError) {
                console.warn("Error deleting existing marker:", deleteError);
              }
            }
            await context.sync();
          });
        } catch (clearError) {
          console.warn("Error clearing existing markers:", clearError);
        }

        // Main scanning logic
        await Word.run(async (context) => {
          try {
            const body = context.document.body;

            let skip = 0;
            while (true) {
              try {
                const paras = body.paragraphs;
                paras.load({ select: "style,text,isListItem,leftIndent,firstLineIndent", top: BATCH_SIZE, skip });
                await context.sync();
                
                if (paras.items.length === 0) break;

                const tocSkip = await computeTocSkips(context, paras);

                for (let i = 0; i < paras.items.length; i++) {
                  try {
                    const p = paras.items[i];
                    if (tocSkip[i]) continue;

                    // Check list compliance
                    await checkBulletedListCompliance(context, p, rules, aliases, issues);

                    const raw = p.style || "";
                    const std = normalizeStyle(raw, aliases);
                    const txt = p.text || "";

                    // Skip placeholder/empty content
                    if (txt.includes("    ") || 
                        txt.includes("Click or tap here to enter text") ||
                        txt.includes("Type here") ||
                        txt.trim() === "" ||
                        /^(\[.*\]|\{.*\}|\(.*\))$/.test(txt.trim())) {
                      continue;
                    }

                    // Skip headings
                    const isHeading =
                      HEADING_ALLOWED.has(std) ||
                      /^(heading|)\s*\d+/.test(std) ||
                      /^(heading|)\s*\d+/.test((raw||"").toLowerCase());
                    if (isHeading) continue;

                    // Check indentation
                    try {
                      const leftCm  = toCm((p.leftIndent as any) || 0);
                      const firstCm = toCm((p.firstLineIndent as any) || 0);
                      const textStart = leftCm + firstCm;

                      const expected = ((): number | undefined => {
                        if ((indentPolicy as any)[raw]) return indentPolicy[raw];
                        const n = normalizeStyle(raw, aliases);
                        if ((indentPolicy as any)[n]) return (indentPolicy as any)[n];
                        return undefined;
                      })();

                      if (typeof expected === "number") {
                        const trimmedText = txt.trim();
                        const listHeuristic =
                          /^[\u2022\u25E6\u25AA\u25CF\u25CB\u25A0\-]\s+/.test(trimmedText) ||
                          /^(\(?\d{1,3}|\(?[A-Za-z]{1,3}|\(?[IVXLCM]{1,4})[.)]\s+/i.test(trimmedText) ||
                          /^\d+\s+/.test(trimmedText);
                        const isRealList = !!p.isListItem && listHeuristic;

                        if (!isRealList && Math.abs(textStart - expected) > indentTolCm) {
                          try {
                            const tag = await markParagraph(context, p);
                            issues.push({
                              tag, 
                              kind: "indent",
                              message: "Indent " + round2(textStart) + " cm (expected " + round2(expected) + " cm) for style \"" + (raw || std) + "\".",
                              fixMeta: { expectedCm: expected }
                            });
                          } catch (indentMarkError) {
                            console.warn("Error marking indent issue:", indentMarkError);
                          }
                        }
                      }
                    } catch (indentCheckError) {
                      console.warn("Error checking indentation:", indentCheckError);
                    }
                  } catch (paragraphError) {
                    console.warn(`Error processing paragraph ${i}:`, paragraphError);
                  }
                }

                skip += paras.items.length;
              } catch (batchError) {
                console.error(`Error processing batch starting at ${skip}:`, batchError);
                skip += BATCH_SIZE; // Continue with next batch
                if (skip > 10000) break; // Safety break to avoid infinite loop
              }
            }

            // Caption checking
            let cskip = 0;
            while (true) {
              try {
                const caps = body.paragraphs;
                caps.load({ select: "style,text", top: BATCH_SIZE, skip: cskip });
                await context.sync();
                
                if (caps.items.length === 0) break;

                const tocSkip2 = await computeTocSkips(context, caps);

                for (let i = 0; i < caps.items.length; i++) {
                  try {
                    if (tocSkip2[i]) continue;

                    const p   = caps.items[i];
                    const raw = p.style || "";
                    const std = normalizeStyle(raw, aliases);
                    const txt = p.text || "";

                    // Skip placeholder/empty content
                    if (txt.includes("    ") || 
                        txt.includes("Click or tap here to enter text") ||
                        txt.includes("Type here") ||
                        txt.trim() === "" ||
                        /^(\[.*\]|\{.*\}|\(.*\))$/.test(txt.trim())) {
                      continue;
                    }

                    const styleIsFig = figAllowed.has(std) || figAllowed.has(raw);
                    const styleIsTbl = tblAllowed.has(std) || tblAllowed.has(raw);

                    const looksFig = looksLikeCaptionText(txt, FIG_LABELS);
                    const looksTbl = looksLikeCaptionText(txt, TBL_LABELS);

                    const needFigCheck = styleIsFig || looksFig;
                    const needTblCheck = styleIsTbl || looksTbl;
                    if (!needFigCheck && !needTblCheck) continue;

                    let figOK = false, tblOK = false;
                    if (needFigCheck) { 
                      try { 
                        figOK = await hasChapterSeqForLabelRobust(context, p, "Figure"); 
                      } catch (figCheckError) {
                        console.warn("Error checking figure fields:", figCheckError);
                      }
                    }
                    if (needTblCheck) { 
                      try { 
                        tblOK = await hasChapterSeqForLabelRobust(context, p, "Table"); 
                      } catch (tblCheckError) {
                        console.warn("Error checking table fields:", tblCheckError);
                      }
                    }

                    // Figure caption checks
                    if (styleIsFig) {
                      if (!figOK && requireFigSeq) {
                        try {
                          const tag = await markParagraph(context, p);
                          issues.push({ 
                            tag, 
                            kind: "figure-style", 
                            message: "Figure caption uses manual numbering. Expected { STYLEREF 1 \\s }.{ SEQ Figure \\* ARABIC \\s 1 }" 
                          });
                        } catch (figMarkError) {
                          console.warn("Error marking figure issue:", figMarkError);
                        }
                      } else if (figOK) {
                        const rest = descriptionAfterNumber(txt, FIG_LABELS);
                        if (!rest) {
                          try {
                            const tag = await markParagraph(context, p);
                            issues.push({ 
                              tag, 
                              kind: "figure-empty", 
                              message: "Figure caption is empty." 
                            });
                          } catch (figEmptyMarkError) {
                            console.warn("Error marking empty figure:", figEmptyMarkError);
                          }
                        }
                      }
                    } else if (looksFig) {
                      if (!figOK) {
                        if (!hasRealSentenceAfterNumber(txt, FIG_LABELS)) {
                          try {
                            const tag = await markParagraph(context, p);
                            issues.push({
                              tag,
                              kind: "figure-style",
                              message: "Figure-like text uses neither automatic numbering nor the correct caption style."
                            });
                          } catch (figStyleMarkError) {
                            console.warn("Error marking figure style issue:", figStyleMarkError);
                          }
                        }
                      } else {
                        try {
                          const tag = await markParagraph(context, p);
                          issues.push({
                            tag,
                            kind: "figure-style",
                            message: "Figure caption uses the wrong style. Apply the designated figure caption style."
                          });
                        } catch (figWrongStyleMarkError) {
                          console.warn("Error marking figure wrong style:", figWrongStyleMarkError);
                        }
                      }
                    }

                    // Table caption checks
                    if (styleIsTbl) {
                      if (!tblOK && requireTblSeq) {
                        try {
                          const tag = await markParagraph(context, p);
                          issues.push({ 
                            tag, 
                            kind: "table-style", 
                            message: "Table caption uses manual numbering. Expected { STYLEREF 1 \\s }.{ SEQ Table \\* ARABIC \\s 1 }" 
                          });
                        } catch (tblMarkError) {
                          console.warn("Error marking table issue:", tblMarkError);
                        }
                      } else if (tblOK) {
                        const rest = descriptionAfterNumber(txt, TBL_LABELS);
                        if (!rest) {
                          try {
                            const tag = await markParagraph(context, p);
                            issues.push({ 
                              tag, 
                              kind: "table-empty", 
                              message: "Table caption is empty." 
                            });
                          } catch (tblEmptyMarkError) {
                            console.warn("Error marking empty table:", tblEmptyMarkError);
                          }
                        }
                      }
                    } else if (looksTbl) {
                      if (!tblOK) {
                        if (!hasRealSentenceAfterNumber(txt, TBL_LABELS)) {
                          try {
                            const tag = await markParagraph(context, p);
                            issues.push({
                              tag,
                              kind: "table-style",
                              message: "Table-like text uses neither automatic numbering nor the correct caption style."
                            });
                          } catch (tblStyleMarkError) {
                            console.warn("Error marking table style issue:", tblStyleMarkError);
                          }
                        }
                      } else {
                        if (!styleIsTbl) {
                          try {
                            const tag = await markParagraph(context, p);
                            issues.push({
                              tag,
                              kind: "table-style",
                              message: "Table caption uses the wrong style. Apply the designated table caption style."
                            });
                          } catch (tblWrongStyleMarkError) {
                            console.warn("Error marking table wrong style:", tblWrongStyleMarkError);
                          }
                        }
                      }
                    }
                  } catch (captionError) {
                    console.warn(`Error processing caption ${i}:`, captionError);
                  }
                }

                cskip += caps.items.length;
              } catch (captionBatchError) {
                console.error(`Error processing caption batch starting at ${cskip}:`, captionBatchError);
                cskip += BATCH_SIZE;
                if (cskip > 10000) break;
              }
            }
          } catch (scanError) {
            console.error("Error in main scan logic:", scanError);
            status.textContent = "Scan failed with error: " + scanError.message;
            status.style.color = "#c00";
            return;
          }
        });

        (window as any).__SGC_ISSUES__ = issues;
        renderIssues(issues);

        if (issues.length === 0) { 
          status.textContent = "Scan complete. No issues found."; 
          status.style.color = "#0a0"; 
        } else { 
          status.textContent = "Scan complete. Found " + issues.length + " issue(s)."; 
          status.style.color = "#a60"; 
        }
      } catch (error) {
        console.error("Critical error in scan:", error);
        status.textContent = "Scan failed: " + error.message;
        status.style.color = "#c00";
      }
    });
  } catch (error) {
    console.error("Error binding scan:", error);
  }
}

/* ------------------------------ Issue List / Actions with Error Handling ------------------------------ */

function renderIssues(issues: Issue[]) {
  try {
    const list = document.getElementById("issuesList") as HTMLOListElement | null;
    if (!list) return;
    list.innerHTML = "";

    const noFixKinds = new Set<IssueKind>(["figure-style", "table-style"]);

    for (const it of issues) {
      try {
        const li = document.createElement("li");
        li.className = "issue";
        li.dataset.tag = it.tag;
        (li.style as any).cursor = "pointer";

        const msg = document.createElement("div");
        msg.textContent = "[" + it.kind + "] " + it.message;
        li.appendChild(msg);

        if (!noFixKinds.has(it.kind)) {
          const fix = document.createElement("button");
          fix.textContent = "Fix";
          fix.addEventListener("click", (e) => {
            e.stopPropagation();
            fixIssue(it).catch(err => {
              console.error("Error fixing issue:", err);
              // Could show user feedback here
            });
          });
          li.appendChild(fix);
        }

        list.appendChild(li);
      } catch (itemError) {
        console.warn("Error rendering issue item:", itemError);
      }
    }
  } catch (error) {
    console.error("Error rendering issues:", error);
  }
}

function bindIssueRowClicks() {
  try {
    const list = document.getElementById("issuesList");
    if (!list) return;
    
    list.addEventListener("click", (e) => {
      try {
        const row = (e.target as HTMLElement).closest("li.issue") as HTMLLIElement | null;
        if (!row) return;
        const tag = row.dataset.tag;
        if (!tag) return;
        jumpToIssue(tag).catch(err => {
          console.error("Error jumping to issue:", err);
        });
      } catch (error) {
        console.warn("Error handling issue row click:", error);
      }
    });
  } catch (error) {
    console.error("Error binding issue row clicks:", error);
  }
}

async function jumpToIssue(tag: string) {
  try {
    await Word.run(async (ctx) => {
      try {
        const controls = ctx.document.contentControls.getByTag(ISSUE_TAG_PREFIX + tag);
        controls.load("items"); 
        await ctx.sync();
        
        if (controls.items.length > 0) { 
          controls.items[0].select(); 
          await ctx.sync(); 
        }
      } catch (jumpError) {
        console.warn("Error jumping to specific issue:", jumpError);
      }
    });
  } catch (error) {
    console.error("Critical error jumping to issue:", error);
  }
}

/* ------------------------------ Fix Logic with Error Handling ------------------------------ */

async function fixIssue(it: Issue) {
  try {
    const rules = getRules() || {};
    const aliases: Record<string, string> = rules?.meta?.styleNameAliases || {};
    const bulletRule = rules?.rules?.styles?.bullets ?? {};
    const numberedRule = rules?.rules?.styles?.numberedLists ?? {};
    
    const allowedBulletNames: string[] = (bulletRule.allowed ?? ["TCC 1","TCC 2","TCC 3","TCC 4"]);
    const allowedNumberedNames: string[] = (numberedRule.allowed ?? ["TCC 1","TCC 2","TCC 3"]);
    
    const indentPolicy: Record<string, number> = rules?.rules?.indentation?.expectedCmByStyle || {};
    const allowedBulletIndentsCm: number[] = (bulletRule.allowedIndentsCm ?? [0.71,1.21,1.71,2.21]);
    const allowedNumberedIndentsCm: number[] = (numberedRule.allowedIndentsCm ?? [0.71,1.21,1.71]);

    const capRules = rules?.rules?.styles?.captions || {};
    const figRule = capRules.figure || {};
    const tblRule = capRules.table || {};
    const figStyle = Array.isArray(figRule.style) ? figRule.style[0] : (figRule.style || "TCC  ");
    const tblStyles = (tblRule.styles || tblRule.style || ",TCC  ,TCC");
    const tblStyle = Array.isArray(tblStyles) ? tblStyles[0] : tblStyles;

    await Word.run(async (ctx) => {
      try {
        const controls = ctx.document.contentControls.getByTag(ISSUE_TAG_PREFIX + it.tag);
        controls.load("items"); 
        await ctx.sync();
        
        if (controls.items.length === 0) return;
        
        const cc = controls.items[0];
        const para = cc.getRange().paragraphs.getFirst();
        para.load(["style","isListItem","leftIndent","firstLineIndent","text"]); 
        await ctx.sync();

        switch (it.kind) {
          case "bullet-style": {
            try {
              const fixMeta = it.fixMeta || {};
              const allowedStyles = fixMeta.allowedStyles || allowedBulletNames;
              const allowedIndents = fixMeta.allowedIndents || allowedBulletIndentsCm;
              const currentIndent = fixMeta.currentIndent || 0;
              
              let bestMatch = 0;
              let minDistance = Math.abs(currentIndent - allowedIndents[0]);
              
              for (let i = 1; i < allowedIndents.length; i++) {
                const distance = Math.abs(currentIndent - allowedIndents[i]);
                if (distance < minDistance) {
                  minDistance = distance;
                  bestMatch = i;
                }
              }
              
              const targetStyle = allowedStyles[bestMatch];
              const targetIndent = allowedIndents[bestMatch];
              
              para.style = targetStyle;
              (para as any).leftIndent = toPt(targetIndent);
              (para as any).firstLineIndent = 0;
              
              await ctx.sync();
              console.log(`[FIX] Applied ${targetStyle} with ${targetIndent}cm indent`);
            } catch (bulletFixError) {
              console.error("Error fixing bullet style:", bulletFixError);
            }
            break;
          }
          case "numbered-style": {
            try {
              const fixMeta = it.fixMeta || {};
              const allowedStyles = fixMeta.allowedStyles || allowedNumberedNames;
              const allowedIndents = fixMeta.allowedIndents || allowedNumberedIndentsCm;
              const currentIndent = fixMeta.currentIndent || 0;
              
              let bestMatch = 0;
              let minDistance = Math.abs(currentIndent - allowedIndents[0]);
              
              for (let i = 1; i < allowedIndents.length; i++) {
                const distance = Math.abs(currentIndent - allowedIndents[i]);
                if (distance < minDistance) {
                  minDistance = distance;
                  bestMatch = i;
                }
              }
              
              const targetStyle = allowedStyles[bestMatch];
              const targetIndent = allowedIndents[bestMatch];
              
              para.style = targetStyle;
              (para as any).leftIndent = toPt(targetIndent);
              (para as any).firstLineIndent = 0;
              
              await ctx.sync();
              console.log(`[FIX] Applied ${targetStyle} with ${targetIndent}cm indent`);
            } catch (numberedFixError) {
              console.error("Error fixing numbered style:", numberedFixError);
            }
            break;
          }
          case "indent": {
            try {
              const expectedCm: number = it.fixMeta?.expectedCm ?? (() => {
                const raw = para.style || "";
                if (raw in indentPolicy) return indentPolicy[raw];
                const n = normalizeStyle(raw, aliases);
                if (n in indentPolicy) return indentPolicy[n];
                const txt = para.text || "";
                const isBulletType = /^[\u2022\u25E6\u25AA\u25CF\u25CB\u25A0\-]\s+/.test(txt.trim());
                return isBulletType ? allowedBulletIndentsCm[0] : allowedNumberedIndentsCm[0];
              })();
              const curFirst = toCm((para.firstLineIndent as any) || 0);
              const newLeft = expectedCm - curFirst;
              (para as any).leftIndent = toPt(newLeft);
              await ctx.sync();
            } catch (indentFixError) {
              console.error("Error fixing indent:", indentFixError);
            }
            break;
          }
          case "figure-empty": {
            try {
              para.style = figStyle; 
              await ctx.sync();
              para.select("End"); 
              await ctx.sync();
            } catch (figEmptyFixError) {
              console.error("Error fixing empty figure:", figEmptyFixError);
            }
            break;
          }
          case "table-empty": {
            try {
              para.style = tblStyle; 
              await ctx.sync();
              para.select("End"); 
              await ctx.sync();
            } catch (tblEmptyFixError) {
              console.error("Error fixing empty table:", tblEmptyFixError);
            }
            break;
          }
        }

        try {
          cc.delete(true);
          await ctx.sync();
        } catch (deleteError) {
          console.warn("Error deleting content control after fix:", deleteError);
        }
      } catch (fixRunError) {
        console.error("Error in Word.run for fix:", fixRunError);
      }
    });
  } catch (error) {
    console.error("Critical error in fixIssue:", error);
    throw error; // Re-throw so caller can handle
  }
}